    <div class="img-container">
      <img v-for="(image, index) in images" :key="index" :src="image.src" alt="Event Image" class="img-item"
        @click="openModal(image.src, image.caption)" />
    </div>
    <div v-if="showModal" class="modal" @click.self="closeModal">
      <div class="modal-content">
        <img :src="selectedImage" alt="Large Image" class="modal-img" />
        <p class="modal-caption">"{{ selectedCaption }}"</p>
      </div>
    </div>
