<script>
export default {
  name: "Olds",
  data() {
    return {
      images: [
        {
          src: require("@/assets/imgs/pano-old.webp"),
          caption: "old",
        },
        {
          src: require("@/assets/imgs/pano-new.webp"),
          caption: "new",
