        {
          src: require("@/assets/imgs/pano-hot.webp"),
          caption: "hot",
        },
      ],
      showModal: false,
      selectedImage: null,
      selectedCaption: null,
    };
  },
  methods: {
    openModal(image, caption) {
      this.selectedImage = image;
      this.selectedCaption = caption;
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
      this.selectedImage = null;
      this.selectedCaption = null;
    },
  },
};
</script>

